import React, { useEffect, useState, useCallback } from 'react';
import {
  View,
  Text,
  Button,
  StyleSheet,
  FlatList,
  TextInput,
  Alert,
  RefreshControl,
  Platform
} from 'react-native';
import { Picker } from '@react-native-picker/picker'; // Only using @react-native-picker/picker
import AsyncStorage from '@react-native-async-storage/async-storage';
import Updater from '../components/Updater';
import api from '../services/api';
import * as Location from 'expo-location';
import TOTPGenerator from '../components/TOTPGenerator';
import QRCodeOverlay from '../services/qrcodeOverlay';

const AppointmentsScreen = ({ navigation }) => {
  const [userData, setUserData] = useState({});
  const [appointments, setAppointments] = useState([]);
  const [showBookingForm, setShowBookingForm] = useState(false);
  const [visible, setVisible] = useState(false);
  const [selectedRefID, setSelectedRefID] = useState('');
  const [refreshing, setRefreshing] = useState(false);
  const [bookingData, setBookingData] = useState({
    searchQuery: '',
    appointmentType: '', 
    destinationID: '',
    localBranchID: '',
    localConfigID: '',
    ppn: '',
  });

  const [cancellationReason, setCancellationReason] = useState('Cancelled by patient');
  
  const whoIsPatient = [    //no longer in use
    { user: userData.user, ppn: userData.ppn },
    { user: 'Jane Smith', ppn: 'jane_smith' },
    { user: 'Mary Johnson', ppn: 'mary_johnson' },
  ];

  const appointmentTypes = [
    {label: 'General Outpatient Appointment', nonproprietaryName: 'general outpatient consultation'},
    {label: 'Dental Clinic Appointment', nonproprietaryName: 'general dental consultation'},
    {label: 'Eye Clinic Consultation', nonproprietaryName: 'general ophthalmology consultation'},
    {label: 'Physiotherapy Clinic', nonproprietaryName: 'general physiotherapy session'},
  ];

  const [destinationOptions, setDestinationOptions] = useState([]);

  useEffect(()=>{
    const destinationSearch = async ()=>{
      try {
        const { status } = await Location.requestForegroundPermissionsAsync();
        if (status !== 'granted') {
          Alert.alert('Permission Denied', 'Location access is required.');
          return;
        }

        const location = await Location.getCurrentPositionAsync({});
        if (!location || !location.coords) {
          Alert.alert('Location Error', 'Unable to fetch location.');
        }

        const dgotp = await TOTPGenerator();
        
        const token = await AsyncStorage.getItem('userToken');
        const response = await api.get(
          `/search_destinations?appointmentType=${encodeURIComponent(bookingData.appointmentType)}&q=${bookingData.searchQuery}&latitude=${location.coords.latitude}&longitude=${location.coords.longitude}`,
          {
            headers: { Authorization: `Bearer ${token}`, dgotp: dgotp, },
          }
        );

        const { destinationOptions } = response.data;
        setDestinationOptions(destinationOptions);

      } catch (err) {
        console.error('Error Response:', err.response?.data);
        setError(
          err.response?.data?.message || 'An error occurred. Please check your credentials and try again.'
        );
      }
    }

    destinationSearch();
  }, [userData?.token, bookingData.appointmentType, bookingData.searchQuery]);

  const fetchUpdatedData = async () => {
    try {
      const updatedUserData = await Updater();
      if (updatedUserData) {
        setUserData(updatedUserData);
        setAppointments(updatedUserData.appointments || []);
      } else {
        const userDataString = await AsyncStorage.getItem('userData');
        const parsedData = userDataString ? JSON.parse(userDataString) : {};
        setUserData(parsedData);
        setAppointments(parsedData.appointments || []);
      }
    } catch (error) {
      console.error('Error fetching updated data:', error);
    }
  };

  useEffect(() => {
    fetchUpdatedData();
  }, [refreshing]);

  const onRefresh = useCallback(() => {
    fetchUpdatedData();
  }, []);




  const showQRCode = (refID) => {
    setSelectedRefID(refID);
    setVisible(true);
  };


  const handleCancelAppointment2 = async (appointment) => {
    const token = await AsyncStorage.getItem('userToken');
    if (!token) {
      Alert.alert('Error', 'User not authenticated.');
      return;
    }
  
    if (!userData?.ppn) {
      Alert.alert('Error', 'No dependant selected.');
      return;
    }
  
    if (!cancellationReason) {
      Alert.alert('Error', 'Please provide a cancellation reason.');
      return;
    }
  
    // Just show confirmation and handle the rest inside 'onPress'
    Alert.alert(
      'Confirm Cancellation',
      'Appointment cancellation is irreversible. Do you want to proceed?',
      [
        {
          text: 'Cancel',
          onPress: () => console.log('Cancellation aborted'),
          style: 'cancel',
        },
        {
          text: 'Confirm',
          onPress: async () => {
            try {
              const freshToken = await AsyncStorage.getItem('userToken');
              if (!freshToken) {
                Alert.alert('Error', 'User not authenticated.');
                return;
              }
  
              const dgotp = await TOTPGenerator();
  
              const response = await api.post(
                '/cancel_appointment',
                {
                  ppn: userData.ppn,
                  sku: appointment.sku,
                  ticketID: appointment.ticketID,
                  cancellationReason: cancellationReason,
                },
                {
                  headers: {
                    Authorization: `Bearer ${freshToken}`,
                    dgotp: dgotp,
                  },
                }
              );
  
              const { message } = response.data;
              Alert.alert('Message', message);
              fetchUpdatedData();
              setShowBookingForm(false);
            } catch (error) {
              console.error('Error canceling appointment:', error);
              Alert.alert(
                'Error',
                error.response?.data?.message ||
                  error.message ||
                  'An unexpected error occurred.'
              );
            }
          },
        },
      ],
      { cancelable: false }
    );
  };


  //This is the version compatible with web build, as Alert does not work in webbuild
  const handleCancelAppointment = async (appointment) => {
    const token = await AsyncStorage.getItem('userToken');
    if (!token) {
      window.alert('Error', 'User not authenticated.');
      return;
    }
  
    if (!userData?.ppn) {
      window.alert('Error', 'No dependant selected.');
      return;
    }
  
    if (!cancellationReason) {
      window.alert('Error', 'Please provide a cancellation reason.');
      return;
    }
  
    const confirmed = window.confirm('Do you want to cancel the appointment?');
    if (!confirmed) {
      console.log('Operation aborted');
      return;
    }
  
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        window.alert('User not authenticated.');
        return;
      }
  
      let dgotp = '123456'; // Temporary test value  replace if TOTPGenerator works on web
  
      // If TOTPGenerator works in web, use it:
      try {
        dgotp = await TOTPGenerator();
      } catch (e) {
        console.warn('TOTPGenerator failed in web build:', e);
      }
  
      const response = await api.post(
        '/cancel_appointment',
        {
          ppn: userData.ppn,
          sku: appointment.sku,
          ticketID: appointment.ticketID,
          cancellationReason: cancellationReason,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            dgotp: dgotp,
          },
        }
      );
  
      const { message } = response.data;
      window.alert(`Success: ${message}`);
      //setDestinationOptions([]);
      //setShowBookingForm(false);
      fetchUpdatedData();
    } catch (error) {
      console.error('Error cancelling appointment:', error);
      if (error.response?.data?.message) {
        window.alert(error.response.data.message);
      } else {
        window.alert('An unexpected error occurred.');
      }
    }
  };
  


  
//This is the main function and it's the right one for mobile aps
  const handleBookAppointment2 = async () => {
    const { ppn, appointmentType, destinationID, localBranchID, localConfigID } = bookingData;
  
    if (!appointmentType || !destinationID || !localBranchID || !localConfigID) {
      Alert.alert('Error', 'All fields are required to book an appointment.');
      return;
    }
  
    if (!ppn) {
      Alert.alert('Error', 'No patient selected.');
      return;
    }
  
    // Use Alert for confirmation (as React Native doesn't have confirm)
    Alert.alert(
      'Confirm Submission',
      'Do you want to submit the appointment request?',
      [
        {
          text: 'Cancel',
          onPress: () => console.log('Submission cancelled'),
          style: 'cancel',
        },
        {
          text: 'Confirm',
          onPress: async () => {
            try {
              const token = await AsyncStorage.getItem('userToken');
              if (!token) {
                Alert.alert('Error', 'User not authenticated.');
                return;
              }
  
              const dgotp = await TOTPGenerator(); // Ensure you're generating or retrieving dgotp
  
              const response = await api.post(
                '/book_appointment',
                {
                  ppn: ppn,
                  appointmentType: appointmentType,
                  destinationID: destinationID,
                  localBranchID: localBranchID,
                  localConfigID: localConfigID,
                },
                {
                  headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
                }
              );
  
              const {message} = await response.data;
                Alert.alert('Success', message); // Assuming the API responds with a message
                setDestinationOptions([]);
                setShowBookingForm(false); // Hide the booking form
                fetchUpdatedData(); // Refresh appointments
            } catch (error) {
              console.error('Error booking appointment:', error);
            
              if (error.response && error.response.data && error.response.data.message) {
                Alert.alert('Error', error.response.data.message);
              } else {
                Alert.alert('Error', 'An unexpected error occurred.');
              }
            }
          },
        },
      ],
      { cancelable: false } // Prevent closing by tapping outside
    );
  };


  //This is the version compatible with web build, as Alert does not work in webbuild
  const handleBookAppointment = async () => {
    const { ppn, appointmentType, destinationID, localBranchID, localConfigID } = bookingData;
  
    if (!appointmentType || !destinationID || !localBranchID || !localConfigID) {
      window.alert('All fields are required to book an appointment.');
      return;
    }
  
    if (!ppn) {
      window.alert('No patient selected.');
      return;
    }
  
    const confirmed = window.confirm('Do you want to submit the appointment request?');
    if (!confirmed) {
      console.log('Submission cancelled');
      return;
    }
  
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        window.alert('User not authenticated.');
        return;
      }
  
      let dgotp = '123456'; // Temporary test value  replace if TOTPGenerator works on web
  
      // If TOTPGenerator works in web, use it:
      try {
        dgotp = await TOTPGenerator();
      } catch (e) {
        console.warn('TOTPGenerator failed in web build:', e);
      }
  
      const response = await api.post(
        '/book_appointment',
        {
          ppn,
          appointmentType,
          destinationID,
          localBranchID,
          localConfigID,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            dgotp: dgotp,
          },
        }
      );
  
      const { message } = response.data;
      window.alert(`Success: ${message}`);
      setDestinationOptions([]);
      setShowBookingForm(false);
      fetchUpdatedData();
    } catch (error) {
      console.error('Error booking appointment:', error);
      if (error.response?.data?.message) {
        window.alert(error.response.data.message);
      } else {
        window.alert('An unexpected error occurred.');
      }
    }
  };
  
  

  const renderAppointment = ({ item }) => (
    <View style={styles.appointmentRow}>
      <View style={styles.appointmentInfo}>
        <Text style={styles.infoText}>Appointment: {item.nonproprietaryName.toUpperCase()}</Text>
        <Text style={styles.infoText}>Location: {item.location}</Text>
        <Text style={styles.infoText}>Status: {item.appointmentStatus}</Text>
        <Text style={styles.infoText}>Service Pin: {item.servicePin}</Text>
      </View>
      <View>
        <Button
          title="Cancel"
          color="#ff4444"
          onPress={() => handleCancelAppointment(item)}
        />
        <Button
          title="QR Code"
          color="#4CAF50"
          onPress={() => showQRCode(item.referenceID)}
        />
        <QRCodeOverlay
        visible={visible}
        qrValue={selectedRefID}
        onClose={() => setVisible(false)}
      />
      </View>
    </View>
  );

  return (
    <View style={styles.container}>
      {!showBookingForm && (
        <View>
          <View style={styles.appointmentDisplay}>
            <FlatList
              data={appointments}
              keyExtractor={(item) => item?.ticketID.toString()}
              renderItem={renderAppointment}
              ListEmptyComponent={<Text style={styles.emptyText}>No appointments found.</Text>}
              refreshControl={<RefreshControl refreshing={refreshing} onRefresh={onRefresh} />}
            />
          </View>

          <Button
            title="Book New Appointment"
            onPress={() => setShowBookingForm(true)}
            color="#008080"
          />
        </View>
      )}

      {showBookingForm && (
        <View style={styles.bookingForm}>
          <Text style={styles.formTitle}>Book a New Appointment</Text>


          {/* Dropdown for Appointment Type Selection */}
          <View style={styles.pickerContainer}>
            <Picker
              selectedValue={bookingData.appointmentType}
              onValueChange={(value) => setBookingData({ ...bookingData, appointmentType: value })}
              style={styles.picker}
            >
              <Picker.Item label="Select Appointment Type" value="" />
              {appointmentTypes.map((type) => (
                <Picker.Item key={type.value} label={type.label} value={type.nonproprietaryName} />
              ))}
            </Picker>
          </View>


          {/* Dropdown for Destination Selection */}
          <View style={styles.pickerContainer}>
            <Picker
              selectedValue={bookingData.localConfigID || ''} // Ensure localConfigID is used as selectedValue
              onValueChange={(value) => {
                // Find the selected destination object based on the selected localConfigID
                const selectedDestination = destinationOptions.find(
                  (destination) => destination.localConfigID === value
                );

                if (selectedDestination) {
                  // If a destination is found, update bookingData with all relevant information
                  setBookingData({
                    ...bookingData,
                    localConfigID: selectedDestination.localConfigID,
                    localBranchID: selectedDestination.localBranchID,
                    destinationID: selectedDestination.destinationID,
                  });
                } else {
                  // If no destination is found, reset values to empty
                  setBookingData({
                    ...bookingData,
                    localConfigID: '',
                    localBranchID: '',
                    destinationID: '',
                  });
                }
              }}
              style={styles.picker}
            >
              <Picker.Item label="Select Destination" value="" />
              {destinationOptions.map((destination, index) => (
                <Picker.Item
                  key={index}
                  label={destination.destination} // Use destination name as label
                  value={destination.localConfigID} // Use localConfigID as the value
                />
              ))}
            </Picker>
          </View>



          {/* Search field to narrow down search to state, city, or even a particular hospital */}
          <TextInput
            style={styles.input}
            placeholder="Search destination..."
            value={bookingData.searchQuery}
            onChangeText={(text) => setBookingData({ ...bookingData, searchQuery: text })}
          />

        
          {/* Dropdown for Patient Selection ...first option is the user, and the subsequent options are the dependants*/}
          <View style={styles.pickerContainer}>
            <Picker
              selectedValue={bookingData.ppn}
              onValueChange={(value) => setBookingData({ ...bookingData, ppn: value })}
              style={styles.picker}
            >
              <Picker.Item label="Select a patient" value="" />
              <Picker.Item label={userData.user} value={userData.ppn} />
              {userData?.dependants.map((patient) => (
                <Picker.Item key={patient.value} label={patient.user} value={patient.ppn} />
              ))}
            </Picker>
          </View>
      
      
          <View style={styles.buttonContainer}>
            <View style={styles.button}>
              <Button title="Book Appointment" onPress={handleBookAppointment} color="#008080" />
            </View>
            <View style={styles.button}>
              <Button title="Cancel" onPress={() => setShowBookingForm(false)} color="#ff4444" />
            </View>
          </View>
      </View>
      
      )}
    </View>
  );
};


