import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, ActivityIndicator, StyleSheet } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import api from '../services/api';
import SecretKeyGenerator from '../components/SecreKeyGenerator';
import TOTPGenerator from '../components/TOTPGenerator';

const FirstPasswordScreen = ({ navigation }) => {
  const [password, setPassword] = useState('');
  const [passwordRepeat, setPasswordRepeat] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleFirstPasswordRequest = async () => {
    if (!password || !passwordRepeat) {
      setError('Please enter both Private Prescription Number (PPN) and password.');
      return;
    }
  
    setLoading(true);
    setError(null);
  
    try {
      const token = await AsyncStorage.getItem('userToken');
      const secretKey = await SecretKeyGenerator();
      const dgotp = await TOTPGenerator();
  
      const response = await api.post(
        '/first_password',
        { password, passwordRepeat },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            secretKey: secretKey,
            dgotp: dgotp,
          },
        }
      );
  
      if (response.status === 200) {
        const { newToken, userData } = response.data;
  
        // Save new token and user data
        await AsyncStorage.setItem('secretKey', secretKey);   //replace secretKey after a successful MFA
        await AsyncStorage.setItem('userToken', newToken);
        await AsyncStorage.setItem('userData', JSON.stringify(userData));
        navigation.replace('Home');
      } else {
        setError(response.data?.message || 'Unexpected response from the server.');
      }
    } catch (err) {
      setError(
        err.response?.data?.message ||
          'An error occurred. Please check your credentials and try again.'
      );
    } finally {
      setLoading(false);
    }
  };
  

  return (
    <View style={styles.container}>
      {/*<Text style={styles.title}>FIRST PASSWORD</Text>*/}

      {error && <Text style={styles.errorText}>{error}</Text>}

      

      <TextInput
        style={styles.input}
        placeholder="Password"
        secureTextEntry
        value={password}
        onChangeText={setPassword}
        textContentType="password"
      />

      <TextInput
        style={styles.input}
        placeholder="Repeat Password"
        secureTextEntry
        value={passwordRepeat}
        onChangeText={setPasswordRepeat}
        textContentType="password"
      />

      <TouchableOpacity style={styles.button} onPress={handleFirstPasswordRequest} disabled={loading}>
      {loading ? (
          <ActivityIndicator size="small" color="#fff" />
      ) : (
          <Text style={styles.buttonText}>Set Password</Text>
      )}
      </TouchableOpacity>
      <Text style={styles.footerText} onPress={()=>navigation.replace('Login')}>
        <Text style={styles.footerLink}>Back</Text>
      </Text>

    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    backgroundColor: '#FFA500', // Bright orange background
  },
  title: {
    color: 'green',
    fontSize: 28,
    fontWeight: 'bold',
    marginBottom: 40,
  },
  input: {
    height: 50,
    width: '100%',
    borderColor: '#ddd',
    borderWidth: 1,
    borderRadius: 10,
    marginBottom: 20,
    paddingLeft: 15,
    backgroundColor: '#fff',
    fontSize: 16,
  },
  button: {
    height: 50,
    width: '100%',
    backgroundColor: '#008080', // Teal button
    borderRadius: 10,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 20,
    //opacity: loading ? 0.6 : 1,
  },
  buttonText: {
    color: '#fff',
    fontSize: 18,
    fontWeight: 'bold',
  },
  errorText: {
    color: 'red',
    marginBottom: 20,
    fontSize: 14,
  },
  footerText: {
    color: '#fff',
    marginTop: 30,
    fontSize: 14,
  },
  footerLink: {
    //color: '#FFD700', // Gold color
    color: 'green',
    fontWeight: 'bold',
  },
});

export default FirstPasswordScreen;
