import AsyncStorage from '@react-native-async-storage/async-storage';
import api from '../services/api';
import TOTPGenerator from './TOTPGenerator';



const Updater = async () => {
  try {
    
    // Generate OTP using the secretKey
    const dgotp = await TOTPGenerator(); // Make sure to handle it as an async function
    if (!dgotp) {
      console.warn('Failed to generate OTP.');
      return;
    }

    // Get the userToken for authorization
    const token = await AsyncStorage.getItem('userToken');
    if (!token) {
      console.warn('No userToken found. Cannot fetch data.');
      return;
    }

    // Make the API request with the OTP included in the headers
    const response = await api.get('/updated_patient_data', {
      headers: {
        Authorization: `Bearer ${token}`, // Include token in the Authorization header
        dgotp: dgotp, // Include OTP in the headers
      },
    });

    // Check if response is valid
    if (!response.data || !response.data.userData) {
      console.error('Invalid response format:', response.data);
      return;
    }

    console.log('Response received:', response.data);

    // Save the updated user data to AsyncStorage
    const { userData } = response.data;
    await AsyncStorage.setItem('userData', JSON.stringify(userData));

    return userData; // Return the updated user data if needed
  } catch (err) {
    console.error('Error during API request:', err);
    return null; // Return null in case of an error
  }
};


export default Updater;
