import React, { useEffect, useState, useCallback } from 'react';
import {
  View,
  Text,
  Button,
  StyleSheet,
  FlatList,
  Alert,
  RefreshControl,
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import Updater from '../components/Updater';
import api from '../services/api';
import * as Location from 'expo-location'; 
import TOTPGenerator from '../components/TOTPGenerator';

const PrescriptionsScreen = ({ navigation }) => {
  const [userData, setUserData] = useState({});
  const [prescriptions, setPrescriptions] = useState([]);
  const [selectedPrescription, setSelectedPrescription] = useState(null);
  const [pharmacies, setPharmacies] = useState([]);
  const [showPharmacies, setShowPharmacies] = useState(false);
  const [refreshing, setRefreshing] = useState(false);

  const fetchUpdatedData = async () => {
    try {
      const updatedUserData = await Updater();
      if (updatedUserData) {
        setUserData(updatedUserData);
        setPrescriptions(updatedUserData.prescriptions || []);
      } else {
        const userDataString = await AsyncStorage.getItem('userData');
        const parsedData = userDataString ? JSON.parse(userDataString) : {};
        setUserData(parsedData);
        setPrescriptions(parsedData.prescriptions || []);
      }
    } catch (error) {
      console.error('Error in fetching updated data:', error);
    }
  };

  useEffect(() => {
    fetchUpdatedData();
  }, []);

  const onRefresh = useCallback(() => {
    fetchUpdatedData();
  },[])


  const locatePrescriptionItem = async (sku) => {
    try {
      // Request location permissions
      const { status } = await Location.requestForegroundPermissionsAsync();
      if (status !== 'granted') {
        Alert.alert('Permission Denied', 'Location access is required.');
        return;
      }
  
      // Get the user's current location
      const location = await Location.getCurrentPositionAsync({});
      if (!location || !location.coords) {
        Alert.alert('Location Error', 'Unable to fetch location.');
        return;
      }
  
      // Get the user token
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        Alert.alert('Error', 'User not authenticated.');
        return;
      }

      const dgotp = await TOTPGenerator();
  
      // Send request to the API with the sku and location
      const response = await api.get(
        `/locate_prescription_item?sku=${sku}&latitude=${location.coords.latitude}&longitude=${location.coords.longitude}`,
        {
          headers: { 
            Authorization: `Bearer ${token}`,
            dgotp: dgotp,
         },
        }
      );
  
      if (response.status === 200) {
        // Set pharmacies list and show them
        setPharmacies(response.data.pharmacies || []);
        setShowPharmacies(true);
      } else {
        Alert.alert('Error', 'Failed to fetch pharmacies.');
      }
    } catch (error) {
      console.error('Error locating prescription:', error);
      Alert.alert('Error', 'An unexpected error occurred.');
    }
  };
  

  const renderPrescription = ({ item }) => (
    <View style={styles.prescriptionRow}>
      <View style={styles.prescriptionInfo}>
        <Text style={styles.infoText}>Prescription: {item.prescription}</Text>
        <Text style={styles.infoText}>Source: {item.source}</Text>
        <Text style={styles.infoText}>Date: {item.date}</Text>
        <Text style={styles.infoText}>Status: {item.status}</Text>
        <Text style={styles.infoText}>Service Pin: {item.servicePin}</Text>
      </View>
      <Button
        title="Locate Item"
        color="#008080"
        onPress={() => locatePrescriptionItem(item.sku)}
      />
    </View>
  );

  const renderPharmacy = ({ item }) => (
    <View style={styles.pharmacyRow}>
      <Text style={styles.infoText}>Pharmacy: {item.pharmacy}</Text>
      <Text style={styles.infoText}>Brand: {item.proprietaryName}</Text>
      <Text style={styles.infoText}>Unit Price: {item.unitPrice}</Text>
      <Text style={styles.infoText}>Location: {item.address}</Text>
      <Text style={styles.infoText}>Distance: {item.distance}</Text>
    </View>
  );

  return (
    <View style={styles.container}>
      {!showPharmacies && (
        <FlatList
          data={prescriptions}
          keyExtractor={(item, index) => index.toString()} // Use sku
          renderItem={renderPrescription}
          ListEmptyComponent={<Text style={styles.emptyText}>No prescriptions found.</Text>}
          refreshControl={ // This is where RefreshControl is used
            <RefreshControl
              refreshing={refreshing} // Determines if the refresh indicator is visible
              onRefresh={onRefresh}  // Callback function triggered when pulled down
            />
          }
        />
      )}
      {showPharmacies && (
        <View>
          <Text style={styles.sectionTitle}>Nearby Pharmacies</Text>
          <FlatList
            data={pharmacies}
            keyExtractor={(item, index) => index.toString()} // Fallback to index if id doesn't exist
            renderItem={renderPharmacy}
          />
          <Button
            title="Back to Prescriptions"
            onPress={() => {setShowPharmacies(false); setPharmacies([]);}}
            color="#ff4444"
          />
        </View>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    marginTop: 50,
    flex: 1,
    padding: 20,
    backgroundColor: '#f9f9f9',
  },
  prescriptionRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 15,
    marginVertical: 10,
    backgroundColor: '#fff',
    borderRadius: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 3,
  },
  prescriptionInfo: {
    flex: 1,
    marginRight: 10,
  },
  infoText: {
    fontSize: 14,
    color: '#333',
  },
  pharmacyRow: {
    padding: 15,
    marginVertical: 10,
    backgroundColor: '#fff',
    borderRadius: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 3,
  },
  sectionTitle: {
    fontSize: 18,
    fontWeight: 'bold',
    marginVertical: 10,
  },
  emptyText: {
    textAlign: 'center',
    fontSize: 16,
    color: '#999',
    marginTop: 20,
  },
  logoutButton: {
    marginTop: 20,
  },
});

export default PrescriptionsScreen;
