import React, { useEffect, useState, useCallback } from 'react';
import {
  View,
  Text,
  Button,
  StyleSheet,
  FlatList,
  Alert,
  RefreshControl,
  Platform
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import Updater from '../components/Updater';
import api from '../services/api';
import * as FileSystem from 'expo-file-system';
import * as Sharing from 'expo-sharing';
import { saveAs } from 'file-saver'; // Optional: you can also use this if you install it
import TOTPGenerator from '../components/TOTPGenerator';

const CorrespondenceScreen = ({ navigation }) => {
  const [userData, setUserData] = useState({});
  const [correspondence, setCorrespondence] = useState([]);
  const [refreshing, setRefreshing] = useState(false);


  const fetchUpdatedData = async () => {
    try {
      const updatedUserData = await Updater();
      if (updatedUserData) {
        setUserData(updatedUserData);
        setCorrespondence(updatedUserData.correspondence || []);
      } else {
        const userDataString = await AsyncStorage.getItem('userData');
        const parsedData = userDataString ? JSON.parse(userDataString) : {};
        setUserData(parsedData);
        setCorrespondence(parsedData.correspondence || []);
      }
    } catch (error) {
      console.error('Error in fetching updated data:', error);
    }
  };

  useEffect(() => {
    fetchUpdatedData();
  }, []);

  const onRefresh = useCallback(() => {
    fetchUpdatedData();
  },[])

  const showAlert = (title, message) => {
    if (Platform.OS === 'web') {
      window.alert(`${title}: ${message}`);
    } else {
      Alert.alert(title, message);
    }
  };

  const handleDownloadCorrespondence = async (correspondenceReference) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        showAlert('Error', 'User not authenticated.');
        return;
      }
  
      const dgotp = await TOTPGenerator();
  
      const response = await api.get(
        `/download_correspondence?correspondenceReference=${correspondenceReference}`,
        {
          headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
          responseType: 'arraybuffer',
        }
      );
  
      if (response.status === 200) {
        const filename = `Correspondence_${correspondenceReference}.pdf`;
  
        if (Platform.OS === 'web') {
          // Convert arraybuffer to Blob for web
          const blob = new Blob([response.data], { type: 'application/pdf' });
  
          // Create a temporary link to trigger download
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          a.remove();
          window.URL.revokeObjectURL(url);
  
          showAlert('Success', 'PDF downloaded.');
        } else {
          const base64Data = btoa(
            new Uint8Array(response.data).reduce((data, byte) => data + String.fromCharCode(byte), '')
          );
  
          const fileUri = FileSystem.documentDirectory + filename;
          await FileSystem.writeAsStringAsync(fileUri, base64Data, {
            encoding: FileSystem.EncodingType.Base64,
          });
  
          if (await Sharing.isAvailableAsync()) {
            await Sharing.shareAsync(fileUri);
          } else {
            showAlert('Success', 'PDF downloaded.');
          }
        }
      } else {
        showAlert('Error', 'Failed to download correspondence letter.');
      }
    } catch (error) {
      console.error('Error during correspondence download:', error);
      showAlert('Error', 'An unexpected error occurred.');
    }
  };
  

  const handleDownloadCorrespondence3 = async (correspondenceReference) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        showAlert('Error', 'User not authenticated.');
        return;
      }
  
      const dgotp = await TOTPGenerator();
  
      const response = await api.get(
        `/download_correspondence?correspondenceReference=${correspondenceReference}`,
        {
          headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
          responseType: 'arraybuffer',
        }
      );
  
      if (response.status === 200) {
        const base64Data = btoa(
          new Uint8Array(response.data).reduce((data, byte) => data + String.fromCharCode(byte), '')
        );
  
        const fileUri = FileSystem.documentDirectory + `Correspondence_${correspondenceReference}.pdf`;
  
        await FileSystem.writeAsStringAsync(fileUri, base64Data, {
          encoding: FileSystem.EncodingType.Base64,
        });
  
        if (await Sharing.isAvailableAsync()) {
          await Sharing.shareAsync(fileUri);
        } else {
          showAlert('Success', 'PDF file has been downloaded successfully!');
        }
      } else {
        showAlert('Error', 'Failed to download correspondence letter.');
      }
    } catch (error) {
      console.error('Error during correspondence download:', error);
      showAlert('Error', 'An unexpected error occurred.');
    }
  };
  

  //this is the main function.
  const handleDownloadCorrespondence2 = async (correspondenceReference) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        Alert.alert('Error', 'User not authenticated.');
        return;
      }

      const dgotp = await TOTPGenerator();
  
      // Step 1: Fetch binary data from API
      const response = await api.get(
        `/download_correspondence?correspondenceReference=${correspondenceReference}`,
        {
          headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
          responseType: 'arraybuffer', // Ensure response is in binary format
        }
      );
  
      if (response.status === 200) {
        // Step 2: Convert arraybuffer to Base64
        const base64Data = btoa(
          new Uint8Array(response.data)
            .reduce((data, byte) => data + String.fromCharCode(byte), '')
        );
  
        const fileUri = FileSystem.documentDirectory + `Correspondence_${correspondenceReference}.pdf`;
  
        // Step 3: Write the Base64 data to a local file
        await FileSystem.writeAsStringAsync(fileUri, base64Data, {
          encoding: FileSystem.EncodingType.Base64,
        });
  
        // Step 4: Share or open the file
        if (await Sharing.isAvailableAsync()) {
          await Sharing.shareAsync(fileUri);
        } else {
          Alert.alert('Success', 'PDF file has been downloaded successfully!');
        }
      } else {
        Alert.alert('Error', 'Failed to download correspondence letter.');
      }
    } catch (error) {
      console.error('Error during correspondence download:', error);
      Alert.alert('Error', 'An unexpected error occurred.');
    }
  };
  

  const renderCorrespondence = ({ item }) => (
    <View style={styles.prescriptionRow}>
      <View style={styles.prescriptionInfo}>
        <Text style={styles.infoText}>Title: {item.noteTitle}</Text>
        <Text style={styles.infoText}>Source: {item.source}</Text>
        <Text style={styles.infoText}>Issued On: {item.issuedOn}</Text>
      </View>
      <Button
        title="Download"
        color="#008080"
        onPress={() => handleDownloadCorrespondence(item.correspondenceReference)}
      />
    </View>
  );


  return (
    <View style={styles.container}>
        <FlatList
          data={correspondence}
          keyExtractor={(item, index) => index.toString()} // Use reference as key
          renderItem={renderCorrespondence}
          ListEmptyComponent={<Text style={styles.emptyText}>No correspondence found.</Text>}
          refreshControl={ // trigger the function to fetch patient's updated data whever screen is dragged down
            <RefreshControl
              refreshing={refreshing} // Determines if the refresh indicator is visible
              onRefresh={onRefresh}  // Callback function triggered when pulled down
            />
          }
        />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    marginTop: 50,
    flex: 1,
    padding: 20,
    backgroundColor: '#f9f9f9',
  },
  prescriptionRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 15,
    marginVertical: 10,
    backgroundColor: '#fff',
    borderRadius: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 3,
  },
  prescriptionInfo: {
    flex: 1,
    marginRight: 10,
  },
  infoText: {
    fontSize: 14,
    color: '#333',
  },
  pharmacyRow: {
    padding: 15,
    marginVertical: 10,
    backgroundColor: '#fff',
    borderRadius: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 3,
  },
  sectionTitle: {
    fontSize: 18,
    fontWeight: 'bold',
    marginVertical: 10,
  },
  emptyText: {
    textAlign: 'center',
    fontSize: 16,
    color: '#999',
    marginTop: 20,
  },
  logoutButton: {
    marginTop: 20,
  },
});

export default CorrespondenceScreen;
