// QRCodeOverlay.js
import React from 'react';
import { Modal, View, Text, TouchableOpacity, StyleSheet } from 'react-native';
import QRCode from 'react-native-qrcode-svg';

export default function QRCodeOverlay({ visible, qrValue, onClose }) {
  return (
    <Modal visible={visible} transparent animationType="fade">
      <View style={styles.modalBackground}>
        <View style={styles.modalContainer}>
          <Text style={styles.title}>Scan Appointment QR</Text>
          <QRCode value={qrValue} size={200} />
          <TouchableOpacity onPress={onClose} style={styles.closeButton}>
            <Text style={styles.closeButtonText}>Close</Text>
          </TouchableOpacity>
        </View>
      </View>
    </Modal>
  );
}

const styles = StyleSheet.create({
  modalBackground: { flex: 1, backgroundColor: 'rgba(0,0,0,0.6)', justifyContent: 'center', alignItems: 'center' },
  modalContainer: { backgroundColor: 'white', padding: 20, borderRadius: 12, alignItems: 'center' },
  title: { fontSize: 18, marginBottom: 20 },
  closeButton: { marginTop: 20, backgroundColor: '#ddd', padding: 10, borderRadius: 6 },
  closeButtonText: { fontWeight: '600' },
});
