import React from 'react';
import { StyleSheet } from 'react-native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import ProfileScreen from '../screens/ProfileScreen';
import PrescriptionsScreen from '../screens/PrescriptionsScreen';
import AppointmentsScreen from '../screens/AppointmentsScreen';
import CorrespondenceScreen from '../screens/Correspondence';
import DependantsScreen from '../screens/DependantsScreen';
import { FontAwesome, MaterialIcons } from '@expo/vector-icons';

const Tab = createBottomTabNavigator();

const RootNavigator = () => {
  return (
    <Tab.Navigator initialRouteName="Profile"
      screenOptions={{
        //headerStyle: styles.headerStyle, // Customizes the header background
        //headerTintColor: 'white', // Sets the text color for the title
        //headerTitleStyle: styles.headerTitleStyle, // Customizes the title font style
        //tabBarStyle: styles.tabBarStyle,
        //tabBarLabelStyle: styles.tabBarLabelStyle,
        headerShown: false,
        tabBarActiveTintColor: '#FF6F00', // Color for the active tab
        tabBarInactiveTintColor: 'gray', // Color for inactive tabs
      }}>
      <Tab.Screen style={styles.title} name="Profile" component={ProfileScreen} options={{
          tabBarIcon: ({ color, size }) => (
            <FontAwesome name="user" size={size} color={color} />
          ),
        }}/>
      <Tab.Screen name="Appointments" component={AppointmentsScreen} options={{
          tabBarIcon: ({ color, size }) => (
            <FontAwesome name="calendar" size={size} color={color} />
          ),
        }}/>
      <Tab.Screen name="Correspondence" component={CorrespondenceScreen} options={{
          tabBarIcon: ({ color, size }) => (
            <FontAwesome name="file-text-o" size={size} color={color} />
          ),
        }}/>
      <Tab.Screen name="Dependants" component={DependantsScreen} options={{
          tabBarIcon: ({ color, size }) => (
            <FontAwesome name="child" size={size} color={color} />
          ),
        }}/>
      <Tab.Screen name="Prescriptions" component={PrescriptionsScreen} options={{
          tabBarIcon: ({ color, size }) => (
            <FontAwesome name="medkit" size={size} color={color} />
          ),
        }}/>
    </Tab.Navigator>
  );
};

const styles = StyleSheet.create({
  headerStyle: {
    backgroundColor: '#FF6F00', // Orange background for the header
  },
  headerTitleStyle: {
    fontWeight: 'bold', // Bold title text
    fontSize: 20, // Larger font size for the title
  },
  tabBarStyle: {
    //backgroundColor: '#FF6F00', // Orange background for the navigation bar
    height: 60, // Adjust height as needed
  },
  tabBarLabelStyle: {
    fontSize: 14, // Customize font size
    fontWeight: 'bold', // Bold text for tab labels
    color: 'white', // White text for labels
    backgroundColor: '#FF6F00',
  },
});

export default RootNavigator;
