import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, ActivityIndicator, StyleSheet } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import api from '../services/api';
import SecretKeyGenerator from '../components/SecreKeyGenerator';

const LoginScreen = ({ navigation }) => {
  const [ppn, setPPN] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  

  const handleLogin = async () => {
    if (!ppn || !password) {
      setError('Please enter both Private Prescription Number (PPN) and password.');
      return;
    }
  
    setLoading(true);
    setError(null);

    const secretKey = await SecretKeyGenerator();    //Generates a secret key at login with 

  
    try {
  
      const response = await api.post('/login', {
        ppn: String(ppn), 
        password: String(password), 
        action: 'login',
        secretKey: secretKey,
      });
  
      console.log('Response received:', response.data); // Debugging step
  
      const { token, userData } = response.data;
  
      // Save user info to AsyncStorage
      await AsyncStorage.setItem('userToken', token);
      await AsyncStorage.setItem('secretKey', secretKey);
      await AsyncStorage.setItem('userData', JSON.stringify(userData));
  
      // Navigate to the TOTP screen
      navigation.replace('TOTP');
    } catch (err) {
      console.error('Error Response:', err.response?.data); // Debugging step
      setError(
        err.response?.data?.message || 'An error occurred. Please check your credentials and try again.'
      );
    } finally {
      setLoading(false);
    }
  };
  

  return (
    <View style={styles.container}>
      <Text style={styles.title}>PRESCRIBE.NG</Text>

      {error && <Text style={styles.errorText}>{error}</Text>}

      <TextInput
        style={styles.input}
        placeholder="Private Prescription Number (PPN)"
        value={ppn}
        onChangeText={setPPN}
        keyboardType="default"
        autoCapitalize="none"
        textContentType="username"
      />

      <TextInput
        style={styles.input}
        placeholder="Password"
        secureTextEntry
        value={password}
        onChangeText={setPassword}
        textContentType="password"
      />

    <TouchableOpacity style={styles.button} onPress={handleLogin} disabled={loading}>
    {loading ? (
        <ActivityIndicator size="small" color="#fff" />
    ) : (
        <Text style={styles.buttonText}>Log In</Text>
    )}
    </TouchableOpacity>

      <Text style={styles.footerText} onPress={()=>navigation.replace('PasswordReset')}>
        <Text style={styles.footerLink}>Forgot Password?</Text>
      </Text>

      <Text style={styles.footerText} onPress={()=>navigation.replace('Activation')}>
        <Text style={styles.footerLink}>Account Activation</Text>
      </Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    backgroundColor: '#FFA500', // Bright orange background
  },
  title: {
    color: 'green',
    fontSize: 28,
    fontWeight: 'bold',
    marginBottom: 40,
  },
  input: {
    height: 50,
    width: '100%',
    borderColor: '#ddd',
    borderWidth: 1,
    borderRadius: 10,
    marginBottom: 20,
    paddingLeft: 15,
    backgroundColor: '#fff',
    fontSize: 16,
  },
  button: {
    height: 50,
    width: '100%',
    backgroundColor: '#008080', // Teal button
    borderRadius: 10,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 20,
    //opacity: loading ? 0.6 : 1,
  },
  buttonText: {
    color: '#fff',
    fontSize: 18,
    fontWeight: 'bold',
  },
  errorText: {
    color: 'red',
    marginBottom: 20,
    fontSize: 14,
  },
  footerText: {
    color: '#fff',
    marginTop: 30,
    fontSize: 14,
  },
  footerLink: {
    //color: '#FFD700', // Gold color
    color: 'green',
    fontWeight: 'bold',
  },
});

export default LoginScreen;
