import AsyncStorage from '@react-native-async-storage/async-storage';
import CryptoJS from 'crypto-js';

// Custom TOTP Generation using crypto-js (HMAC-SHA1)
const TOTPGenerator = async (timeStep = 30, digits = 6) => {
  try {
    // Retrieve the secretKey stored in AsyncStorage
    const secretKey = await AsyncStorage.getItem('secretKey');
    if (!secretKey) {
      throw new Error('No secretKey found in AsyncStorage');
    }

    const time = Math.floor(Date.now() / 1000 / timeStep); // Calculate the time step
    const counter = CryptoJS.enc.Hex.parse(time.toString(16).padStart(16, '0')); // Convert time to a 16-byte counter

    const hmac = CryptoJS.HmacSHA1(counter, secretKey); // Generate HMAC with secret key
    const hmacBytes = hmac.toString(CryptoJS.enc.Hex); // Convert HMAC to hex string

    // Extract dynamic 4 bytes from HMAC and generate OTP
    const offset = parseInt(hmacBytes.charAt(hmacBytes.length - 1), 16);
    const otp = parseInt(hmacBytes.substr(offset * 2, 8), 16) & 0x7fffffff;
    const otpCode = otp % Math.pow(10, digits); // Ensure OTP has the desired number of digits

    return otpCode.toString().padStart(digits, '0'); // Return OTP as string with leading zeros
  } catch (error) {
    console.error('Error generating TOTP:', error);
    return null; // Return null in case of error
  }
};

export default TOTPGenerator;
