import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, ActivityIndicator, StyleSheet } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import api from '../services/api';
import SecretKeyGenerator from '../components/SecreKeyGenerator';

const AccountActivationScreen = ({ navigation }) => {
  const [action, setAction] = useState('activation');
  const [ppn, setPPN] = useState('');
  const [nin, setNIN] = useState('');
  const [dob, setDOB] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleAccountActivate = async () => {
    if (!ppn || !nin) {
      setError('Enter a valid PPN and NIN.');
      return;
    }

    setLoading(true);
    setError(null);

  

    try {
      const secretKey = await SecretKeyGenerator();
      //const response = await api.post('/login', { ppn, nin, dob, action, secretKey });
      const response = await api.post('/login', {
        ppn: String(ppn), 
        nin: String(nin), 
        action: 'activation',
        secretKey: secretKey,
      });

      const { token, userData } = response.data;

      if(userData.activationAccountFound){
      // Save user info to AsyncStorage
      await AsyncStorage.setItem('userToken', token);
      await AsyncStorage.setItem('secretKey', secretKey)
      await AsyncStorage.setItem('userData', JSON.stringify(userData));

      // Navigate to the TOTP screen
      navigation.replace('TOTP');
      }
    } catch (err) {
      setError(
        err.response?.data?.message || 'An error occurred. Please check your credentials and try again.'
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Account Activation</Text>

      {error && <Text style={styles.errorText}>{error}</Text>}

      <TextInput
        style={styles.input}
        placeholder="Private Prescription Number (PPN)"
        value={ppn}
        onChangeText={setPPN}
        keyboardType="default"
        autoCapitalize="none"
        textContentType="username"
      />

      <TextInput
        style={styles.input}
        placeholder="National Identification Number(NIN)"
        value={nin}
        onChangeText={setNIN}
        keyboardType="default"
        autoCapitalize="none"
        textContentType="username"
      />

    <TouchableOpacity style={styles.button} onPress={handleAccountActivate} disabled={loading}>
    {loading ? (
        <ActivityIndicator size="small" color="#fff" />
    ) : (
        <Text style={styles.buttonText}>Activate Account</Text>
    )}
    </TouchableOpacity>

    <Text style={styles.footerText} onPress={()=>navigation.replace('Login')}>
        <Text style={styles.footerLink}>Back to Login Page</Text>
    </Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    backgroundColor: '#FFA500', // Bright orange background
  },
  title: {
    color: 'green',
    fontSize: 28,
    fontWeight: 'bold',
    marginBottom: 40,
  },
  input: {
    height: 50,
    width: '100%',
    borderColor: '#ddd',
    borderWidth: 1,
    borderRadius: 10,
    marginBottom: 20,
    paddingLeft: 15,
    backgroundColor: '#fff',
    fontSize: 16,
  },
  button: {
    height: 50,
    width: '100%',
    backgroundColor: '#008080', // Teal button
    borderRadius: 10,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 20,
    //opacity: loading ? 0.6 : 1,
  },
  buttonText: {
    color: '#fff',
    fontSize: 18,
    fontWeight: 'bold',
  },
  errorText: {
    color: 'red',
    marginBottom: 20,
    fontSize: 14,
  },
  footerText: {
    color: '#fff',
    marginTop: 30,
    fontSize: 14,
  },
  footerLink: {
    //color: '#FFD700', // Gold color
    color: 'green',
    fontWeight: 'bold',
  },
});

export default AccountActivationScreen;
