import React, { useEffect, useState, useCallback } from 'react';
import {
  View,
  Text,
  Button,
  StyleSheet,
  FlatList,
  ScrollView,
  Image,
  Alert,
  TouchableOpacity,
  RefreshControl,
  Platform
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import Updater from '../components/Updater';
import api from '../services/api';
import * as Location from 'expo-location'; 
import * as FileSystem from 'expo-file-system';
import * as Sharing from 'expo-sharing';
import TOTPGenerator from '../components/TOTPGenerator';
import QRCodeOverlay from '../services/qrcodeOverlay';

const DependantsScreen = ({ navigation }) => {
  const [userData, setUserData] = useState({});
  const [CurrentPage, setCurrentPage] = useState('dependantsList');
  const [subPage, setSubPage] = useState('prescriptions');   //For toggling between dependant's prescriptions list and nearest locations of dependant's prescription items
  const [dependants, setPrescriptions] = useState([]);
  const [refreshing, setRefreshing] = useState(false);
  const [selectedDependant, setSelectedDependant] = useState(null);
  const [pharmacies, setPharmacies] = useState([]);
  const [cancellationReason, setCancellationReason] = useState(`Cancelled by watcher: ${userData.user}`);
  const [showDependants, setShowPharmacies] = useState(false);

  //Displaying qr code for dependants
  const [visible, setVisible] = useState(false);
  const [selectedRefID, setSelectedRefID] = useState('');
  const showQRCode = (refID) => {
    setSelectedRefID(refID);
    setVisible(true);
  };

  const fetchUpdatedData = async () => {
    try {
      const updatedUserData = await Updater();
      if (updatedUserData) {
        setUserData(updatedUserData);
        setPrescriptions(updatedUserData.dependants || []);
        const updatedDep = dependants.find(dep => dep.ppn === selectedDependant?.ppn);
        if (updatedDep) {
          setSelectedDependant(updatedDep);
        }
      } else {
        const userDataString = await AsyncStorage.getItem('userData');
        const parsedData = userDataString ? JSON.parse(userDataString) : {};
        setUserData(parsedData);
        setPrescriptions(parsedData.dependants || []);
      }
    } catch (error) {
      console.error('Error in fetching updated data:', error);
    }
  };

  useEffect(() => {
    fetchUpdatedData();
  }, []);

  const onRefresh = useCallback(() => {
    fetchUpdatedData();
  },[])


  const handleSelectedDependant = async (item) => {
    setSelectedDependant(item);
    setCurrentPage('dependant');
  };
  

  const renderDependants = ({ item }) => (
    <View style={styles.prescriptionRow}>
      <View style={styles.prescriptionInfo}>
        <Text style={styles.infoText}>Dependant: {item.user}</Text>
        <Text style={styles.infoText}>PPN: {item.ppn}</Text>
        <Text style={styles.infoText}>DOB: {item.dob} ({item.age})</Text>
        </View>
        <Button
        title="Open"
        color="#008080"
        onPress={() => handleSelectedDependant(item)}
      />  
    </View>
  );


  //Pharmacy functions
  const locatePrescriptionItem = async (sku) => {
    try {
      // Request location permissions
      const { status } = await Location.requestForegroundPermissionsAsync();
      if (status !== 'granted') {
        Alert.alert('Permission Denied', 'Location access is required.');
        return;
      }
  
      // Get the user's current location
      const location = await Location.getCurrentPositionAsync({});
      if (!location || !location.coords) {
        Alert.alert('Location Error', 'Unable to fetch location.');
        return;
      }
  
      // Get the user token
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        Alert.alert('Error', 'User not authenticated.');
        return;
      }

      const dgotp = await TOTPGenerator();
  
      // Send request to the API with the sku and location
      const response = await api.get(
        `/locate_prescription_item?sku=${sku}&latitude=${location.coords.latitude}&longitude=${location.coords.longitude}`,
        {
          headers: { 
            Authorization: `Bearer ${token}`,
            dgotp: dgotp,
         },
        }
      );
  
      if (response.status === 200) {
        // Set pharmacies list and show them
        setPharmacies(response.data.pharmacies || []);
        setShowPharmacies(true);
      } else {
        Alert.alert('Error', 'Failed to fetch pharmacies.');
      }
    } catch (error) {
      console.error('Error locating prescription:', error);
      Alert.alert('Error', 'An unexpected error occurred.');
    }
  };

  const renderPrescription = ({ item }) => (
    <View style={styles.prescriptionRow}>
      <View style={styles.prescriptionInfo}>
        <Text style={styles.infoText}>Prescription: {item.prescription}</Text>
        <Text style={styles.infoText}>Source: {item.source}</Text>
        <Text style={styles.infoText}>Date: {item.date}</Text>
        <Text style={styles.infoText}>Status: {item.status}</Text>
        <Text style={styles.infoText}>Service Pin: {item.servicePin}</Text>
      </View>
      <Button
        title="Locate Item"
        color="#008080"
        onPress={() => {locatePrescriptionItem(item.sku); setSubPage('itemLocation');}}
      />
    </View>
  );

  const renderPharmacy = ({ item }) => (
    <View style={styles.pharmacyRow}>
      <Text style={styles.infoText}>Pharmacy: {item.pharmacy}</Text>
      <Text style={styles.infoText}>Brand: {item.proprietaryName}</Text>
      <Text style={styles.infoText}>Unit Price: {item.unitPrice}</Text>
      <Text style={styles.infoText}>Location: {item.address}</Text>
      <Text style={styles.infoText}>Distance: {item.distance}</Text>
    </View>
  );


  //CORRESPONDENCE
  const renderCorrespondence = ({ item }) => (
    <View style={styles.prescriptionRow}>
      <View style={styles.prescriptionInfo}>
        <Text style={styles.infoText}>Title: {item.noteTitle}</Text>
        <Text style={styles.infoText}>Source: {item.source}</Text>
        <Text style={styles.infoText}>Issued On: {item.issuedOn}</Text>
      </View>
      <Button
        title="Download"
        color="#008080"
        onPress={() => handleDownloadCorrespondence(item.correspondenceReference)}
      />
    </View>
  );


  const showAlert = (title, message) => {
    if (Platform.OS === 'web') {
      window.alert(`${title}: ${message}`);
    } else {
      Alert.alert(title, message);
    }
  };

  const handleDownloadCorrespondence = async (correspondenceReference) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        showAlert('Error', 'User not authenticated.');
        return;
      }
  
      const dgotp = await TOTPGenerator();
  
      const response = await api.get(
        `/download_correspondence?correspondenceReference=${correspondenceReference}`,
        {
          headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
          responseType: 'arraybuffer',
        }
      );
  
      if (response.status === 200) {
        const filename = `Correspondence_${correspondenceReference}.pdf`;
  
        if (Platform.OS === 'web') {
          // Convert arraybuffer to Blob for web
          const blob = new Blob([response.data], { type: 'application/pdf' });
  
          // Create a temporary link to trigger download
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          a.remove();
          window.URL.revokeObjectURL(url);
  
          showAlert('Success', 'PDF downloaded.');
        } else {
          const base64Data = btoa(
            new Uint8Array(response.data).reduce((data, byte) => data + String.fromCharCode(byte), '')
          );
  
          const fileUri = FileSystem.documentDirectory + filename;
          await FileSystem.writeAsStringAsync(fileUri, base64Data, {
            encoding: FileSystem.EncodingType.Base64,
          });
  
          if (await Sharing.isAvailableAsync()) {
            await Sharing.shareAsync(fileUri);
          } else {
            showAlert('Success', 'PDF downloaded.');
          }
        }
      } else {
        showAlert('Error', 'Failed to download correspondence letter.');
      }
    } catch (error) {
      console.error('Error during correspondence download:', error);
      showAlert('Error', 'An unexpected error occurred.');
    }
  };

  const handleDownloadCorrespondence2 = async (correspondenceReference) => {
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        Alert.alert('Error', 'User not authenticated.');
        return;
      }

      const dgotp = await TOTPGenerator();
  
      // Step 1: Fetch binary data from API
      const response = await api.get(
        `/download_correspondence?correspondenceReference=${correspondenceReference}`,
        {
          headers: { Authorization: `Bearer ${token}`, dgotp: dgotp },
          responseType: 'arraybuffer', // Ensure response is in binary format
        }
      );
  
      if (response.status === 200) {
        // Step 2: Convert arraybuffer to Base64
        const base64Data = btoa(
          new Uint8Array(response.data)
            .reduce((data, byte) => data + String.fromCharCode(byte), '')
        );
  
        const fileUri = FileSystem.documentDirectory + `Correspondence_${correspondenceReference}.pdf`;
  
        // Step 3: Write the Base64 data to a local file
        await FileSystem.writeAsStringAsync(fileUri, base64Data, {
          encoding: FileSystem.EncodingType.Base64,
        });
  
        // Step 4: Share or open the file
        if (await Sharing.isAvailableAsync()) {
          await Sharing.shareAsync(fileUri);
        } else {
          Alert.alert('Success', 'PDF file has been downloaded successfully!');
        }
      } else {
        Alert.alert('Error', 'Failed to download correspondence letter.');
      }
    } catch (error) {
      console.error('Error during correspondence download:', error);
      Alert.alert('Error', 'An unexpected error occurred.');
    }
  };



  //APPOINTMENTS

  const handleCancelAppointment2 = async (appointment) => {
    const token = await AsyncStorage.getItem('userToken');
    if (!token) {
      Alert.alert('Error', 'User not authenticated.');
      return;
    }
  
    if (!selectedDependant?.ppn) {
      Alert.alert('Error', 'No dependant selected.');
      return;
    }
  
    if (!cancellationReason) {
      Alert.alert('Error', 'Please provide a cancellation reason.');
      return;
    }
  
    // Just show confirmation and handle the rest inside 'onPress'
    Alert.alert(
      'Confirm Cancellation',
      'Appointment cancellation is irreversible. Do you want to proceed?',
      [
        {
          text: 'Cancel',
          onPress: () => console.log('Cancellation aborted'),
          style: 'cancel',
        },
        {
          text: 'Confirm',
          onPress: async () => {
            try {
              const freshToken = await AsyncStorage.getItem('userToken');
              if (!freshToken) {
                Alert.alert('Error', 'User not authenticated.');
                return;
              }
  
              const dgotp = await TOTPGenerator();
  
              const response = await api.post(
                '/cancel_appointment',
                {
                  ppn: selectedDependant.ppn,
                  sku: appointment.sku,
                  ticketID: appointment.ticketID,
                  cancellationReason: cancellationReason,
                },
                {
                  headers: {
                    Authorization: `Bearer ${freshToken}`,
                    dgotp: dgotp,
                  },
                }
              );
  
              const { message } = response.data;
              Alert.alert('Message', message);
              fetchUpdatedData();
              //setShowBookingForm(false);
            } catch (error) {
              console.error('Error canceling appointment:', error);
              Alert.alert(
                'Error',
                error.response?.data?.message ||
                  error.message ||
                  'An unexpected error occurred.'
              );
            }
          },
        },
      ],
      { cancelable: false }
    );
  };



  //This is the version compatible with web build, as Alert does not work in webbuild
  const handleCancelAppointment = async (appointment) => {
    const token = await AsyncStorage.getItem('userToken');
    if (!token) {
      window.alert('Error', 'User not authenticated.');
      return;
    }
  
    if (!userData?.ppn) {
      window.alert('Error', 'No dependant selected.');
      return;
    }
  
    if (!cancellationReason) {
      window.alert('Error', 'Please provide a cancellation reason.');
      return;
    }
  
    const confirmed = window.confirm('Do you want to cancel the appointment?');
    if (!confirmed) {
      console.log('Operation aborted');
      return;
    }
  
    try {
      const token = await AsyncStorage.getItem('userToken');
      if (!token) {
        window.alert('User not authenticated.');
        return;
      }
  
      let dgotp = '123456'; // Temporary test value  replace if TOTPGenerator works on web
  
      // If TOTPGenerator works in web, use it:
      try {
        dgotp = await TOTPGenerator();
      } catch (e) {
        console.warn('TOTPGenerator failed in web build:', e);
      }
  
      const response = await api.post(
        '/cancel_appointment',
        {
          ppn: userData.ppn,
          sku: appointment.sku,
          ticketID: appointment.ticketID,
          cancellationReason: cancellationReason,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            dgotp: dgotp,
          },
        }
      );
  
      const { message } = response.data;
      window.alert(`Success: ${message}`);
      //setDestinationOptions([]);
      //setShowBookingForm(false);
      fetchUpdatedData();
    } catch (error) {
      console.error('Error cancelling appointment:', error);
      if (error.response?.data?.message) {
        window.alert(error.response.data.message);
      } else {
        window.alert('An unexpected error occurred.');
      }
    }
  };
  


  const renderAppointment = ({ item }) => (
    <View style={styles.appointmentRow}>
      <View style={styles.appointmentInfo}>
        <Text style={styles.infoText}>Appointment: {item.nonproprietaryName.toUpperCase()}</Text>
        <Text style={styles.infoText}>Location: {item.location}</Text>
        <Text style={styles.infoText}>Status: {item.appointmentStatus}</Text>
        <Text style={styles.infoText}>Service Pin: {item.servicePin}</Text>
      </View>
      <View>
        <Button
          title="Cancel"
          color="#ff4444"
          onPress={() => handleCancelAppointment(item)}
        />
        <Button
          title="QR Code"
          color="#4CAF50"
          onPress={() => showQRCode(item.referenceID)}
        />
        <QRCodeOverlay
        visible={visible}
        qrValue={selectedRefID}
        onClose={() => setVisible(false)}
      />
      </View>
    </View>
  );


  return (
    <View style={styles.page}>
      {CurrentPage=='dependantsList' && 
      <View style={styles.container}>
          <FlatList
            data={dependants}
            keyExtractor={(item, index) => index.toString()} // Use sku
            renderItem={renderDependants}
            ListEmptyComponent={<Text style={styles.emptyText}>No dependants found.</Text>}
            refreshControl={ // trigger the function to fetch patient's updated data whever screen is dragged down
              <RefreshControl
                refreshing={refreshing} // Determines if the refresh indicator is visible
                onRefresh={onRefresh}  // Callback function triggered when pulled down
              />
            }
          />
          
      </View>
      }

      {CurrentPage=='dependant' && 
      <ScrollView 
        style={styles.container}
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
      >
        <View style={styles.profileHeader}>
          {userData?.photoUrl ? (
            <Image source={{ uri: selectedDependant.photoUrl }} style={styles.profileImage} />
          ) : (
            <View style={styles.placeholderImage}>
              <Text style={styles.placeholderText}>No Image</Text>
            </View>
          )}
          <Text style={styles.userName}>{selectedDependant?.user || 'Guest'}</Text>
        </View>
        
        <View style={styles.profileDetails}>
          <Text style={styles.detailText}>
            <Text style={styles.bold}>PPN: </Text>{selectedDependant?.ppn || 'Not found'}
          </Text>
          <Text style={styles.detailText}>
            <Text style={styles.bold}>Gender: </Text>{selectedDependant?.gender || 'Not found'} ({selectedDependant?.age}yrs)
          </Text>
          <Text style={styles.detailText}>
            <Text style={styles.bold}>DOB: </Text>{selectedDependant?.dob || 'Not found'} ({selectedDependant?.age}yrs)
          </Text>
          <Text style={styles.detailText}>
            <Text style={styles.bold}>Address: </Text>{selectedDependant?.address || 'Not found'}
          </Text>
          <Text style={styles.detailText}>
            <Text style={styles.bold}>Credit: </Text>{selectedDependant?.credit || 0.00}
          </Text>
        </View>


        <View style={styles.profileDetails}>
          <Text style={styles.sectionTitle}>Sponsors/HMO:  </Text>
            {selectedDependant?.policies?.length > 0 ? (
            selectedDependant.policies.map((policy, index) => (
              <View key={index} style={styles.policyRow}>
                <Text style={styles.policyName}>{policy.sponsor.toUpperCase()}</Text>
                <Text style={styles.policyId}>Plan: {policy.plan}({policy.policyNumber})</Text>
                <Text style={styles.policyId}>Contact: {policy.contact}</Text>
              </View>
            ))
          ) : (
            <Text style={styles.noSponsorText}>No sponsor info available</Text>
          )}
        </View>


        <View style={styles.profileDetails}>
          <Text style={styles.sectionTitle}>Prescriptions</Text>
          {subPage==='prescriptions' &&
          <FlatList
            data={selectedDependant?.prescriptions}
            keyExtractor={(item, index) => index.toString()} // Use sku
            renderItem={renderPrescription}
            ListEmptyComponent={<Text style={styles.emptyText}>No prescriptions found.</Text>}
            refreshControl={ // This is where RefreshControl is used
              <RefreshControl
                refreshing={refreshing} // Determines if the refresh indicator is visible
                onRefresh={onRefresh}  // Callback function triggered when pulled down
              />
            }
          />
          }

          {subPage==='itemLocation' &&
          <View>
          <FlatList
            data={pharmacies}
            keyExtractor={(item, index) => index.toString()} // Use sku
            renderItem={renderPharmacy}
            ListEmptyComponent={<Text style={styles.emptyText}>No prescriptions found.</Text>}
            refreshControl={ // This is where RefreshControl is used
              <RefreshControl
                refreshing={refreshing} // Determines if the refresh indicator is visible
                onRefresh={onRefresh}  // Callback function triggered when pulled down
              />
            }
          />
          <Button
            title="Back to prescriptions"
            color="#ff4444"
            onPress={() => {setSubPage('prescriptions'); setPharmacies([]);}}
          />
          </View>
          }
        </View>


        <View style={styles.profileDetails}>
          <Text style={styles.sectionTitle}>Appointments</Text>
          <FlatList
              data={selectedDependant?.appointments}
              keyExtractor={(item) => item?.ticketID.toString()}
              renderItem={renderAppointment}
              ListEmptyComponent={<Text style={styles.emptyText}>No appointments found.</Text>}
              refreshControl={<RefreshControl refreshing={refreshing} onRefresh={onRefresh} />}
            />
        </View>

        <View style={styles.profileDetails}>
          <Text style={styles.sectionTitle}>Correspondence</Text>
          <FlatList
            data={selectedDependant?.correspondence}
            keyExtractor={(item, index) => index.toString()} // Use reference as key
            renderItem={renderCorrespondence}
            ListEmptyComponent={<Text style={styles.emptyText}>No correspondence found.</Text>}
            refreshControl={ // trigger the function to fetch patient's updated data whever screen is dragged down
              <RefreshControl
                refreshing={refreshing} // Determines if the refresh indicator is visible
                onRefresh={onRefresh}  // Callback function triggered when pulled down
              />
            }
          />
        </View>


        
        <View style={styles.logoutContainer}>
          <Button title="Close" onPress={() => {setSelectedDependant({}); setCurrentPage('dependantsList');}} color="#FF6F00" />
        </View>
      </ScrollView>
      }
    </View>
  );
};

