import React, { useEffect, useState } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import AsyncStorage from '@react-native-async-storage/async-storage';

import AccountActivationScreen from './authenticationScreens/AccountActivateScreen';
import FirstPasswordScreen from './authenticationScreens/FirstPassword';
import LoginScreen from './authenticationScreens/LoginScreen';
import TOTPscreen from './authenticationScreens/TOTPscreen';
import HomeScreen from './screens/HomeScreen';
import PasswordReset from './authenticationScreens/PasswordReset';

const Stack = createStackNavigator();

export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(null);  // Initialize as null to wait for AsyncStorage check

  useEffect(() => {
    const checkToken = async () => {
      const token = await AsyncStorage.getItem('userToken');
      const userString = await AsyncStorage.getItem('userData'); 
      const userData = JSON.parse(userString);
      if (token && userData?.action==='login' && userData?.tokenValidity==='valid') {
        setIsLoggedIn(true);
      } else {
        setIsLoggedIn(false);
      }
    };
    checkToken();
  }, []);  // Only run once when the app is loaded

  if (isLoggedIn === null) {
    // Loading state while checking token, you can return a loading screen here if necessary
    return null;
  }

  return (
    <NavigationContainer>
      {isLoggedIn ? (
        <Stack.Navigator screenOptions={{ headerShown: false }}>
          <Stack.Screen name="Home" component={HomeScreen} />
          <Stack.Screen name="Login" component={LoginScreen} />
          <Stack.Screen name="TOTP" component={TOTPscreen} />
          <Stack.Screen name="Activation" component={AccountActivationScreen} />
          <Stack.Screen name="FirstPassword" component={FirstPasswordScreen} />
          <Stack.Screen name="PasswordReset" component={PasswordReset} />
        </Stack.Navigator>
      ) : (
        <Stack.Navigator screenOptions={{ headerShown: false }}>
          <Stack.Screen name="Login" component={LoginScreen} />
          <Stack.Screen name="TOTP" component={TOTPscreen} />
          <Stack.Screen name="Home" component={HomeScreen} />
          <Stack.Screen name="Activation" component={AccountActivationScreen} />
          <Stack.Screen name="FirstPassword" component={FirstPasswordScreen} />
          <Stack.Screen name="PasswordReset" component={PasswordReset} />
        </Stack.Navigator>
      )}
    </NavigationContainer>
  );
}
