import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, ActivityIndicator, StyleSheet, Alert } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import api from '../services/api';
import TOTPGenerator from '../components/TOTPGenerator';
import SecretKeyGenerator from '../components/SecreKeyGenerator';


const TOTPscreen = ({ navigation }) => {
  const [totp, setTOTP] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);


  
  const handleMultifactorAuthentication = async () => {
    if (!totp) {
      setError('Please enter the TOTP.');
      return;
    }
  
    setLoading(true);
    setError(null);
  
    const token = await AsyncStorage.getItem('userToken'); 
    const secretKey = await SecretKeyGenerator(); 
    if (!token || !secretKey) {
      setError('Authentication token is missing. Please log in again.');
      setLoading(false);
      return;
    }

    const dgotp = await TOTPGenerator();
  
    try {
      const response = await api.post(
        '/multifactor_authentication',
        { totp: String(totp) }, // Payload
        {
          headers: {
            Authorization: `Bearer ${token}`, // Attach token
            secretKey: secretKey,
            dgotp: dgotp,
          },
        }
      );
  
      console.log('Response received:', response.data); // Debugging step
  
      const { newToken, userData } = response.data;
  
      // Save new token and user data
      await AsyncStorage.setItem('secretKey', secretKey);   //replace secretKey after a successful MFA
      await AsyncStorage.setItem('userToken', newToken);
      await AsyncStorage.setItem('userData', JSON.stringify(userData));
  
      // Navigate based on action
      if (userData.action === 'firstPassword') {
        navigation.replace('FirstPassword');
      } else if(userData.action==='login') {
        navigation.replace('Home');
      } 
    } catch (err) {
      console.error('Error Response:', err.response?.data); // Debugging step
      setError(
        err.response?.data?.message ||
        'An error occurred. Please check the TOTP and try again.'
      );
    } finally {
      setLoading(false);
    }
  };
  
  

  return (
    <View style={styles.container}>

      {error && <Text style={styles.errorText}>{error}</Text>}

      <TextInput
        style={styles.input}
        placeholder="Enter OTP"
        value={totp}
        onChangeText={setTOTP}
        keyboardType="default"
        autoCapitalize="none"
        textContentType="username"
      />

    <TouchableOpacity style={styles.button} onPress={handleMultifactorAuthentication} disabled={loading}>
    {loading ? (
        <ActivityIndicator size="small" color="#fff" />
    ) : (
        <Text style={styles.buttonText}>Verify OTP</Text>
    )}
    </TouchableOpacity>

    <Text style={styles.footerText} onPress={()=>navigation.replace('Login')}>
        <Text style={styles.footerLink}>Back to Login Page</Text>
    </Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    backgroundColor: '#FFA500', 
  },
  title: {
    color: 'green',
    fontSize: 28,
    fontWeight: 'bold',
    marginBottom: 40,
  },
  input: {
    height: 50,
    width: '100%',
    borderColor: '#ddd',
    borderWidth: 1,
    borderRadius: 10,
    marginBottom: 20,
    paddingLeft: 15,
    backgroundColor: '#fff',
    fontSize: 16,
  },
  button: {
    height: 50,
    width: '100%',
    backgroundColor: '#008080', // Teal button
    borderRadius: 10,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 20,
    //opacity: loading ? 0.6 : 1,
  },
  buttonText: {
    color: '#fff',
    fontSize: 18,
    fontWeight: 'bold',
  },
  errorText: {
    color: 'red',
    marginBottom: 20,
    fontSize: 14,
  },
  footerText: {
    color: '#fff',
    marginTop: 30,
    fontSize: 14,
  },
  footerLink: {
    //color: '#FFD700', // Gold color
    color: 'green',
    fontWeight: 'bold',
  },
});

export default TOTPscreen;
